import {injectGlobal} from 'styled-components'

injectGlobal`
  @import url('https://fonts.googleapis.com/css?family=Ubuntu:300,400,500,700|Ubuntu+Condensed|Rokkitt:300');
  body{
    font-family: 'Ubuntu Condensed', sans-serif;
    background-color: #fff9c4;
  }
  `


